/* HACKNEY DOWNS AI Styles */

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 50%, #0f3460 100%);
  color: #ecf0f1;
  min-height: 100vh;
}

.App {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 20px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  overflow: hidden;
}

.search-container {
  padding: 20px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.search-input {
  width: 100%;
  padding: 12px 20px;
  border: 2px solid rgba(255, 255, 255, 0.2);
  border-radius: 25px;
  background: rgba(255, 255, 255, 0.1);
  color: #ecf0f1;
  font-size: 16px;
  outline: none;
  transition: all 0.3s ease;
}

.search-input:focus {
  border-color: #ff6b6b;
  background: rgba(255, 255, 255, 0.15);
}

.branding {
  text-align: center;
  margin: 20px 0;
  padding: 10px;
}

.branding p {
  font-size: 48px;
  font-weight: bold;
  margin: 0;
  background: linear-gradient(45deg, #ff6b6b, #4ecdc4, #45b7d1);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-shadow: 2px 2px 4px rgba(0,0,0,0.3);
  animation: glow 2s ease-in-out infinite alternate;
}

@keyframes glow {
  from { text-shadow: 2px 2px 4px rgba(0,0,0,0.3); }
  to { text-shadow: 2px 2px 4px rgba(255, 107, 107, 0.5); }
}

.downs {
  font-family: 'Impact', sans-serif;
  font-size: 72px;
  color: #ff6b6b;
  text-shadow: 3px 3px 6px rgba(0,0,0,0.5);
}

.chat-history {
  flex: 1;
  overflow-y: auto;
  padding: 20px;
  scrollbar-width: thin;
  scrollbar-color: rgba(255, 107, 107, 0.3) transparent;
}

.chat-history::-webkit-scrollbar {
  width: 6px;
}

.chat-history::-webkit-scrollbar-track {
  background: transparent;
}

.chat-history::-webkit-scrollbar-thumb {
  background: rgba(255, 107, 107, 0.3);
  border-radius: 3px;
}

.welcome-message {
  text-align: center;
  padding: 40px 20px;
}

.welcome-message h2 {
  font-size: 36px;
  margin-bottom: 30px;
  background: linear-gradient(45deg, #ff6b6b, #4ecdc4);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.features {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-top: 20px;
  flex-wrap: wrap;
}

.features span {
  background: rgba(255, 255, 255, 0.1);
  padding: 8px 16px;
  border-radius: 20px;
  font-size: 14px;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.message {
  margin-bottom: 20px;
  padding: 15px;
  border-radius: 15px;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  animation: fadeIn 0.5s ease-in;
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

.message.user {
  background: linear-gradient(135deg, rgba(255, 107, 107, 0.2), rgba(78, 205, 196, 0.2));
  margin-left: 100px;
}

.message.ai {
  background: rgba(255, 255, 255, 0.08);
  margin-right: 100px;
}

.avatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: linear-gradient(45deg, #ff6b6b, #4ecdc4);
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  color: white;
  margin-bottom: 10px;
  float: left;
  margin-right: 15px;
}

.message-header {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 10px;
}

.message-header strong {
  color: #ff6b6b;
  font-size: 16px;
}

.timestamp {
  font-size: 12px;
  color: rgba(255, 255, 255, 0.6);
}

.model-badge {
  background: rgba(255, 107, 107, 0.2);
  padding: 2px 8px;
  border-radius: 10px;
  font-size: 10px;
  color: #ff6b6b;
}

.message-content {
  line-height: 1.6;
}

.message-content pre {
  background: rgba(0, 0, 0, 0.3);
  padding: 15px;
  border-radius: 10px;
  overflow-x: auto;
  margin: 10px 0;
}

.message-content code {
  background: rgba(0, 0, 0, 0.3);
  padding: 2px 6px;
  border-radius: 4px;
  font-family: 'Courier New', monospace;
}

.image-results {
  margin-top: 15px;
}

.generated-image {
  max-width: 100%;
  border-radius: 10px;
  margin: 10px 0;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}

.search-results {
  margin-top: 15px;
}

.search-result {
  background: rgba(255, 255, 255, 0.05);
  padding: 15px;
  border-radius: 10px;
  margin: 10px 0;
  border-left: 4px solid #ff6b6b;
}

.search-result h4 {
  margin-bottom: 8px;
}

.search-result a {
  color: #4ecdc4;
  text-decoration: none;
}

.search-result a:hover {
  text-decoration: underline;
}

.chat-input {
  display: flex;
  gap: 10px;
  padding: 20px;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  align-items: flex-end;
}

.chat-input textarea {
  flex: 1;
  padding: 12px 20px;
  border: 2px solid rgba(255, 255, 255, 0.2);
  border-radius: 25px;
  background: rgba(255, 255, 255, 0.1);
  color: #ecf0f1;
  font-size: 16px;
  outline: none;
  resize: none;
  transition: all 0.3s ease;
}

.chat-input textarea:focus {
  border-color: #ff6b6b;
  background: rgba(255, 255, 255, 0.15);
}

.voice-btn, .image-btn, .code-btn, .web-btn, .settings-btn {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: none;
  background: rgba(255, 255, 255, 0.1);
  color: #ecf0f1;
  font-size: 20px;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.voice-btn:hover, .image-btn:hover, .code-btn:hover, .web-btn:hover, .settings-btn:hover {
  background: rgba(255, 107, 107, 0.3);
  transform: scale(1.1);
}

.voice-btn.recording {
  background: #ff6b6b;
  animation: pulse 1s infinite;
}

@keyframes pulse {
  0% { transform: scale(1); }
  50% { transform: scale(1.1); }
  100% { transform: scale(1); }
}

button {
  padding: 12px 24px;
  border: none;
  border-radius: 25px;
  background: linear-gradient(45deg, #ff6b6b, #4ecdc4);
  color: white;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
  outline: none;
}

button:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 4px 15px rgba(255, 107, 107, 0.4);
}

button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.image-gen-panel, .code-exec-panel, .web-search-panel, .settings-panel {
  padding: 20px;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  background: rgba(255, 255, 255, 0.02);
}

.image-gen-panel input, .web-search-panel input, .settings-panel select {
  width: 100%;
  padding: 12px 20px;
  margin-bottom: 15px;
  border: 2px solid rgba(255, 255, 255, 0.2);
  border-radius: 25px;
  background: rgba(255, 255, 255, 0.1);
  color: #ecf0f1;
  font-size: 16px;
  outline: none;
}

.code-exec-panel textarea {
  width: 100%;
  padding: 12px 20px;
  margin-bottom: 15px;
  border: 2px solid rgba(255, 255, 255, 0.2);
  border-radius: 15px;
  background: rgba(255, 255, 255, 0.1);
  color: #ecf0f1;
  font-size: 14px;
  outline: none;
  font-family: 'Courier New', monospace;
}

.code-exec-panel select {
  padding: 8px 12px;
  border: 2px solid rgba(255, 255, 255, 0.2);
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.1);
  color: #ecf0f1;
  margin-bottom: 15px;
  outline: none;
}

.action-buttons {
  padding: 20px;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
  align-items: center;
}

.action-buttons textarea {
  flex: 1;
  min-width: 200px;
  padding: 8px 12px;
  border: 2px solid rgba(255, 255, 255, 0.2);
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.1);
  color: #ecf0f1;
  font-size: 14px;
  outline: none;
  resize: vertical;
}

.export-btn, .clear-btn, .train-btn {
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  color: #ecf0f1;
}

.export-btn:hover, .clear-btn:hover, .train-btn:hover {
  background: rgba(255, 255, 255, 0.2);
}

.settings-panel {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
}

.settings-panel label {
  display: flex;
  flex-direction: column;
  gap: 8px;
  font-weight: bold;
}

.settings-panel select {
  padding: 8px 12px;
  border: 2px solid rgba(255, 255, 255, 0.2);
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.1);
  color: #ecf0f1;
  outline: none;
}

.settings-panel input[type="range"] {
  width: 100%;
  appearance: none;
  -webkit-appearance: none;
  height: 6px;
  border-radius: 3px;
  background: rgba(255, 255, 255, 0.2);
  outline: none;
}

.settings-panel input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: #ff6b6b;
  cursor: pointer;
}

.settings-panel input[type="checkbox"] {
  width: 20px;
  height: 20px;
  accent-color: #ff6b6b;
}

/* Responsive Design */
@media (max-width: 768px) {
  .container {
    margin: 10px;
    height: calc(100vh - 20px);
  }

  .branding p {
    font-size: 36px;
  }

  .downs {
    font-size: 48px;
  }

  .features {
    gap: 10px;
  }

  .features span {
    font-size: 12px;
    padding: 6px 12px;
  }

  .chat-input {
    flex-wrap: wrap;
  }

  .voice-btn, .image-btn, .code-btn, .web-btn, .settings-btn {
    width: 40px;
    height: 40px;
    font-size: 16px;
  }

  .action-buttons {
    flex-direction: column;
    align-items: stretch;
  }

  .action-buttons textarea {
    min-width: auto;
  }
}